<template>
  <div class="lottery-wrap">
    <div class="mian-cont">
      <div class="top-box">
        <img src="./imgs/text-everyday.png" alt="">
      </div>

      <div class="btn-box flex-around">
        <router-link tag="span" to="/my-prize" class="router-btn">我的中奖记录</router-link>
        <router-link tag="span" to="/past-prize" class="router-btn">往期中奖名单</router-link>
      </div>

      <div class="no-prize-box" v-if="!prizeInfo">
        <img src="./imgs/img-car.jpg" alt="">
        <ButtonM class="prize-version">{{curVeision}}</ButtonM>
        <span>奖品还在赶来的路上，敬请期待</span>
      </div>
      <template v-else>
        <div class="prize-cont-box">
          <div class="prize-img-box flex">
            <img v-if="prizeInfo.ticketImg" :src="img_baseUrl+prizeInfo.ticketImg" alt="">
          </div>

          <div class="prize-detail-text">
            <ButtonM class="lottery-title">{{prizeInfo.lotteryDay|formatTime}}期奖品</ButtonM>
            <div class="prize-name txt-cut">{{prizeInfo.ticketName}}</div>
            <div class="price-box">
              <span class="text-left">市价： </span>
              <span class="text-price">{{prizeInfo.marketPrice}}</span>
              <span class="text-unit">&nbsp;元</span>
            </div>
            <div class="text-rule">中奖规则：20:00开奖，从当天参 与早安打卡活动中随机抽一位幸 运者，并公示。</div>
          </div>
        </div>
        <div class="detail-img-box">
          <img class="icon-link" src="./imgs/icon-link.png" alt="">
          <img class="icon-link" src="./imgs/icon-link.png" alt="">
          <div class="prize-desc" v-html="prizeInfo.ticketDesc"></div>
        </div>
      </template>

    </div>
  </div>
</template>

<script>
  import ButtonM from '../../components/ButtonM/ButtonM';
  import {currPrize} from "../../api/daylottery";
  import {parseTime} from "../../utils/tools";
  import {baseUrl} from "../../utils/request";
  import {clog} from "../../api/punchin";
  export default {
    name: "lottery-draw",
    components: {ButtonM},
    data() {
      return {
        img_baseUrl: baseUrl.imgURL,
        prizeInfo: null
        // {
        //   lotteryDay: '本期奖品',   // 抽奖期数
        //   ticketName: '奖品还在路上',        // 抽奖名称
        //   marketPrice: 'xxxx',              //市场参考价格
        //   ticketImg: require('./imgs/goods.png'), //奖项列表图片
        //   ticketDesc: '',                   // 奖项说明
        //   detailUrl: []
        // }
      }
    },
    computed: {
      curVeision() {
        return parseTime(new Date(), '{yyyy}.{mm}.{dd}') + '期'
      }
    },
    filters: {
      formatTime(val) {
        if(!val) return ''
        return val.split(' ')[0].replace(/\-/g,'.')
      }
    },
    created(){
      currPrize().then(res => {
        const {result, reply} = res
        if(result.businessCode === '0') {
          const {lotteryDay, lotteryDesc, ticketCog} = reply
          this.prizeInfo = {lotteryDay, lotteryDesc, ...ticketCog}
        }
      })
      clog('天天抽奖', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'当期奖品页','进入当期奖品页']);
    }
   }
</script>
