import request from '../utils/request';

/******天天抽奖******/
/**
 * 查询当天奖项配置
 * @param data {}
 * @returns {AxiosPromise}
 */
export function currPrize() {
  return request({
    url: '/daylottery/currPrize',
    method: 'post',
    data: {}
  })
}

/**
 * 获取往期中奖用户列表
 * @param data {currPeriodFlag, currentPage, count}
 * @returns {AxiosPromise}
 */
export function prizeRecord(data) {
  return request({
    url: '/daylottery/queryPrizeRecord',
    method: 'post',
    data: data
  })
}


